/* estilos.css */
@import url('https://fonts.googleapis.com/css2?family=Indie+Flower&family=Montserrat:wght@100;200;400;700&display=swap');

html, body {
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
    overflow-x: hidden;
}

body {
    background-color: #000000;
    font-family: 'Indie Flower', cursive;
    display: flex;
    flex-direction: column;
    min-height: 100vh;
}

.navbar {
    width: 100%;
    height: 60px;
    background-color: #979797;
    display: flex;
    justify-content: space-between;
    align-items: center;
    box-shadow: 0 2px 5px rgba(161, 156, 156, 0.1);
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1000;
}

.app-container {
    width: 100%;
    height: calc(100vh - 60px);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    margin-top: 60px;
    background-color: #000000;
    overflow-y: auto;
}

.app-container h2{
    font-family: 'Montserrat',sans-serif;
    display: flex;
    justify-content: center;
}


.navbar h1{
    margin-left: 20px ;
    color: #ffffff;
}
h1 {
    font-family: 'Indie Flower', cursive;
    font-weight: 700;
    margin-bottom: 20px;
    display: flex;
    justify-content: center;
}
h3{
    font-family: 'Montserrat',sans-serif;
    font-weight: 400;
}

.form-container {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    max-width: 1200px;
    flex-wrap: wrap;
}

.modern-form {
    background-color: #242424;
    border-radius: 8px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
    padding: 2rem;
    width: 90%;
    max-width: 400px;
    margin: 1rem;
    display: flex;
    flex-direction: column;
    font-family: 'Montserrat', sans-serif;
    align-items: center;
    transition: box-shadow 0.3s ease;
    min-height: 300px;
}

.modern-form:hover {
    box-shadow: 0 0 15px 5px rgba(255, 255, 255, 0.8);
}

.modern-form h2 {
    font-weight: 400;
    margin-bottom: 20px;
}

.modern-form label {
    display: block;
    margin-top: 20px;
    font-weight: 500;
    font-family: 'Montserrat', sans-serif;
}

.modern-form input {
    width: 100%;
    padding: 10px;
    margin-bottom: 16px;
    border: 1px solid #ccc;
    border-radius: 4px;
    transition: border-color 0.3s;
}

.modern-form input:focus {
    border-color: #007bff;
    outline: none;
}

.modern-form button {
    background-color: #4b4b4b;
    font-family: 'Montserrat', sans-serif;
    color: white;
    border: none;
    border-radius: 4px;
    padding: 10px 15px;
    cursor: pointer;
    transition: background-color 0.3s;
}

.modern-form button:hover {
    background-color: #000000;
}

.modern-form h2 {
    display: inline-block;
    margin: 0;
}

/* Estilos para los enlaces del Navbar */
.navbar-links {
    display: flex;
    align-items: center;
}

.navbar a {
    color: white;
    text-decoration: none;
    margin: 0 15px;
    font-family: 'Montserrat', sans-serif;
    font-weight: 400;
    transition: color 0.3s;
}

.navbar a:hover {
    color: #007bff;
}

/* Estilos adicionales para mejorar la responsividad */
@media (max-width: 768px) {
    .form-container {
        flex-direction: column;
        align-items: center;
    }
}

/* Estilo específico para la vista de Inicio */
.inicio {
    background-color: #000000;
    color: white;
    width: 100%;
    height: calc(100vh - 60px);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
}

/* Estilo específico para el formulario de login */
.login-container {
    background-color: black; /* Fondo negro */
    color: white; /* Color del texto blanco */
    width: 100%;
    height: calc(100vh - 60px); /* Altura completa menos la barra de navegación */
    display: flex;
    justify-content: center;
    align-items: center;
}

.login-form {
    background-color: #242424; /* Fondo del formulario */
    border-radius: 8px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.5);
    padding: 40px;
    width: 400px;
    display: flex;
    flex-direction: column;
    font-family: 'Montserrat', sans-serif;
}

/* Estilos para los campos del formulario */
.login-form input {
    background-color: #333; /* Fondo gris oscuro para los campos */
    color: white; /* Color del texto blanco */
    border: 1px solid #ccc; /* Borde gris claro */
    border-radius: 4px;
    padding: 10px;
    margin-bottom: 16px;
    transition: border-color 0.3s;
}

.login-form input:focus {
    border-color: #007bff; /* Color del borde al enfocar */
    outline: none;
}

/* Estilo para el botón */
.login-form button {
    background-color: #4b4b4b;
    font-family: 'Montserrat', sans-serif;
    color: white;
    border: none;
    border-radius: 4px;
    padding: 10px 15px;
    cursor: pointer;
    transition: background-color 0.3s;
}

.login-form button:hover {
    background-color: #000000;
}

/* Estilo para el enlace de registro */
.login-form p a {
    color: white; /* Color blanco para el enlace de registro */
    text-decoration: none; /* Sin subrayado */
}

.login-form p a:hover {
    text-decoration: underline; /* Subrayar al pasar el mouse */
}

/* Estilo para el mensaje de éxito */
.success-message {
    color: green; /* Color del texto del mensaje */
    margin-top: 10px; /* Espacio superior */
    font-weight: bold; /* Negrita */
    text-align: center; /* Centrar el texto */
}

/* Estilos para el h2 de Gastos */
.gastos-header {
    color: white; /* Color del texto blanco */
    text-shadow: 0 0 5px rgba(255, 255, 255, 0.7); /* Sombra de texto blanca */
    margin-top: 20px; /* Espacio superior */
    margin-bottom: 10px; /* Espacio inferior */
    font-family: 'Montserrat', sans-serif; /* Asegúrate de que la fuente sea consistente */
    font-weight: 400;
}

/* Estilos para la tabla en el Dashboard */
table {
    font-family: 'Montserrat', sans-serif;
    width: 100%;
    border-collapse: collapse;
    margin-top: 20px;
}

th, td {
    border: 1px solid #ccc; /* Borde gris claro */
    padding: 10px;
    text-align: left;
}

th {
    background-color: #242424; /* Fondo oscuro para el encabezado */
    color: white; /* Color del texto del encabezado */
}

tbody tr {
    background-color: white; /* Fondo blanco para todas las filas */
    color: #333; /* Color del texto */
}

tbody tr:nth-child(even) {
    background-color: #f9f9f9; /* Color de fondo para filas pares */
}

tbody tr:hover {
    background-color: #ddd; /* Color de fondo al pasar el mouse sobre la fila */
}

/* Estilos para el campo de selección (select) */
.modern-form select {
    width: 110%;
    padding: 10px;
    padding-right: 10px;
    margin-bottom: 16px;
    border: 1px solid #ccc;
    border-radius: 4px;
    font-family: 'Montserrat', sans-serif;
    background-color: #fff;
    transition: border-color 0.3s;
}

.modern-form select:focus {
    border-color: #007bff;
    outline: none;
}

/* Estilos para el contenedor de inputs */
.input-container {
    display: flex;
    flex-direction: column; /* Alinear los inputs verticalmente */
    margin-bottom: 20px; /* Espacio inferior entre inputs y textos */
}

/* Estilos para cada grupo de input y label */
.input-group {
    display: flex;
    flex-direction: column; /* Alinear el label y el input verticalmente */
    width: 100%; /* Asegurarse de que ocupe todo el ancho */
}

/* Estilo para los labels */
.input-group label {
    font-weight: 500;
    font-family: 'Montserrat', sans-serif; /* Fuente Montserrat */
    margin-bottom: 5px; /* Espacio entre el label y el input */
}

/* Estilos para los inputs */
.input-group input {
    width: 100%; /* Asegurarse de que ocupe todo el ancho */
    padding: 10px; /* Espaciado interno */
    border: 1px solid #ccc; /* Borde gris claro */
    border-radius: 4px; /* Bordes redondeados */
    transition: border-color 0.3s; /* Transición para el borde */
}

.input-group input:focus,
.input-group select:focus {
    border-color: #454b49; /* Color del borde al enfocar */
    outline: none; /* Sin contorno */
}

/* Estilos para el contenedor de textos */
.text-container {
    display: flex;
    flex-direction: column; /* Alinear los textos verticalmente */
    align-items: center; /* Centrar los textos */
}

/* Estilos para la barra de navegación */
.navbar {
    width: 100vw;
    height: 60px;
    background-color: rgba(151, 151, 151, 0.9); /* Color de fondo con opacidad */
    display: flex;
    justify-content: space-between;
    align-items: center;
    box-shadow: 0 2px 5px rgba(161, 156, 156, 0.1);
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1000;
    transition: background-color 0.3s ease, opacity 0.3s ease; /* Transición suave para el fondo y la opacidad */
}

/* Estilos para el logo */
.navbar-logo {
    height: 40px; /* Ajusta la altura según sea necesario */
    margin-left: 20px; /* Espacio a la izquierda del logo */
    border-radius: 50%;
}

/* Estilos para el menú desplegable */
.nav-links {
    list-style: none;
    display: flex;
    flex-direction: row; /* Mostrar enlaces en fila por defecto */
    margin: 0;
    padding: 0;
}

.nav-links li {
    margin: 0 15px; /* Espaciado entre enlaces */
}

.menu-icon {
    display: none; /* Ocultar el icono de menú por defecto */
    flex-direction: column; /* Alinear las barras verticalmente */
    cursor: pointer; /* Cambiar el cursor al pasar sobre el icono */
    position: absolute; /* Posicionar el icono de hamburguesa */
    right: 20px; /* Espacio desde la derecha */
    top: 15px; /* Espacio desde la parte superior */
}

.bar {
    height: 3px;
    width: 25px;
    background-color: white; /* Color de las barras */
    margin: 3px 0; /* Espaciado entre las barras */
    transition: 0.3s; /* Transición suave */
}

/* Estilos para la sección de funcionalidades */
.funcionalidades {
    background-color: #242424; /* Fondo oscuro para la sección */
    font-family: 'Montserrat',sans-serif;
    color: white;
    padding: 2rem;
    margin-top: 2rem; /* Espacio superior */
    border-radius: 8px; /* Bordes redondeados */
}

.funcionalidades-container {
    display: flex;
    flex-wrap: wrap; /* Permitir que las funcionalidades se envuelvan */
    justify-content: center; /* Centrar las funcionalidades */
}

.funcionalidad {
    width: 30%; /* Ancho de cada funcionalidad */
    margin: 1rem; /* Espacio entre funcionalidades */
    text-align: center; /* Centrar el texto */
}

.funcionalidad h3 {
    margin-top: 1rem; /* Espacio superior para el título */
}

.funcionalidad p {
    margin-top: 0.5rem; /* Espacio superior para la descripción */
}


@media (max-width: 768px) {
    body {
        background-color: #000000; /* Fondo negro en móviles */
    }

    .app-container {
        height: calc(100vh - 60px); /* Asegurarse de que ocupe toda la altura disponible */
    }

    .modern-form {
        width: 90%; /* Ancho del formulario en pantallas pequeñas */
        padding: 15px; /* Ajustar el padding para pantallas pequeñas */
        height: auto; /* Permitir que la altura se ajuste automáticamente */
    }
}
/* Estilos adicionales para mejorar la responsividad */
@media (max-width: 768px) {
    .form-container {
        flex-direction: column;
        align-items: center;
    }

    .modern-form {
        width: 90%;
        max-width: none;
        padding: 15px;
    }

    .navbar {
        flex-direction: column;
        align-items: center;
    }

    .navbar h1 {
        margin: 10px 0;
    }

    .navbar a {
        margin: 5px 0;
    }
}


/* Estilos para el menú desplegable en móviles */
@media (max-width: 768px) {
    .nav-links {
        display: none; /* Ocultar enlaces por defecto en móviles */
        flex-direction: column; /* Alinear enlaces verticalmente */
        position: absolute; /* Posicionar el menú desplegable */
        top: 60px; /* Justo debajo de la barra de navegación */
        left: 0;
        width: 100%; /* Ancho completo */
        background-color: rgba(75, 75, 75, 0.9); /* Color de fondo del menú */
        z-index: 999; /* Asegurarse de que esté por encima de otros elementos */
        transition: opacity 0.9s ease; /* Transición suave para la opacidad */
    }

    .nav-links.open {
        display: flex; /* Mostrar enlaces cuando el menú está abierto */
        opacity: 1; /* Asegurarse de que la opacidad sea 1 cuando está abierto */
    }

    .menu-icon {
        display: flex; /* Mostrar el icono de menú en móviles */
    }
}



/* Media queries para pantallas más pequeñas */
@media (max-width: 768px) {
    .modern-form {
        width: 80%; /* Ancho del formulario en pantallas pequeñas */
        max-width: none; /* Sin ancho máximo para que ocupe más espacio */
        padding: 15px; /* Ajustar el padding para pantallas pequeñas */
        height: auto; /* Permitir que la altura se ajuste automáticamente */
    }

    .input-group {
        width: 100%; /* Asegurarse de que ocupe todo el ancho */
    }

    .input-group input,
    .input-group select {
        width: 100%; /* Asegurarse de que ocupe todo el ancho */
        padding: 10px; /* Espaciado interno */
        margin-bottom: 16px; /* Espacio inferior */
        border: 1px solid #ccc; /* Borde gris claro */
        border-radius: 4px; /* Bordes redondeados */
        transition: border-color 0.3s; /* Transición para el borde */
    }
    .input-group select{
        width: 110%;
    }
}




/* Ocultar el encabezado en móviles */
@media (max-width: 768px) {
    .inicio h1 {
        display: none; /* Ocultar el encabezado en pantallas pequeñas */
    }
}

/* Aumentar el margen superior en móviles */
@media (max-width: 768px) {
    .app-container {
        margin-top: 80px; /* Aumentar el margen superior para móviles */
    }
}
/* Media query para pantallas con menos de 920px de altura */
@media (max-height: 920px) {
    .app-container {
        height: calc(100vh - 60px); /* Asegurarse de que ocupe toda la altura disponible */
        overflow-y: auto; /* Permitir desplazamiento vertical si el contenido es demasiado alto */
    }

    .modern-form {
        min-height: 200px; /* Ajustar la altura mínima para formularios en pantallas pequeñas */
        padding: 1rem; /* Ajustar el padding para pantallas pequeñas */
    }
}

/* Estilos para la confirmación de cierre de sesión */
.logout-confirmation {
    font-family: 'Montserrat';
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #242424; /* Fondo oscuro */
    color: white; /* Color del texto */
    padding: 10px; /* Espaciado interno */
    border-radius: 5px; /* Bordes redondeados */
    margin-top: 5px; /* Espacio superior */
    position: absolute; /* Posición absoluta para que se coloque debajo del navbar */
    top: 60px; /* Ajustar para que esté justo debajo de la barra de navegación */
    left: 0; /* Alinear a la izquierda */
    width: 100%; /* Ancho completo */
    z-index: 999; /* Asegurarse de que esté por encima de otros elementos */
}

.confirm-button, .cancel-button {
    background-color: #4b4b4b; /* Color de fondo de los botones */
    color: white; /* Color del texto */
    border: none; /* Sin borde */
    border-radius: 4px; /* Bordes redondeados */
    padding: 5px 10px; /* Espaciado interno */
    margin: 5px; /* Espacio entre botones */
    cursor: pointer; /* Cambiar cursor al pasar sobre el botón */
    transition: background-color 0.3s; /* Transición suave */
}

.confirm-button:hover, .cancel-button:hover {
    background-color: #000000; /* Color de fondo al pasar el mouse */
}


